<template>
  <div id="main">
      <div id="home">
        <div class="intro">
          <div class="heading">
            <h1><span class="char" v-for="h in msg.heading1">{{ h }}</span></h1>
            <h1><span class="char" v-for="h in msg.heading2">{{ h }}</span></h1>
          </div>
          <div class="tagline">
            <p>Designer</p>
            <p>Developer</p>
            <p>Illustrator</p>
          </div>
        </div>
      </div>
  </div>
</template>

<script>
import 'gsap'
import animation from '../Animation'
export default {
  name: 'Main',
  data () {
    return {
      title: 'Grotesk',
      msg: {  heading1: 'Design-Driven', heading2: 'Interaction' }
      
    }
  },
  methods: {
    enter(el, done) {
      animation.home.enter(el, done);
    },
    leave(el, done) {
      animation.home.leave(el, done);
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
#main {
  
  
}
.wrapper {
  width: 100%;
  height: 100%;

}

.flex {
  width: 100%;
  height: 100;
  display: flex;
  justify-content: center;
  align-items: center;
}
.intro {
  position: absolute;
  top: 50%;
  left: 5em;
  transform: translateY(-50%);
}

#home {
  font-family: Playfair Display;
  color: #FFFFFF;
  width: 100%;
  height: 100vh;

}

.heading {
  font-size: 2rem;
}

.tagline {
  font-family: HK Grotesk, 'sans-serif';
  margin-top: 1.3em;
  line-height: 1.8em;
}

@media screen and (max-width: 900px) {
    .heading {
      font-size: .9rem;
    }
}

</style>



