<template>
    <div id="about">
        <div class="bg">
            <div class="text_container">
                <span v-for="char in bgName" class="char">{{ char }}</span>
                <div class="line"></div>
            </div>
        </div>
        <div class="flex-container">
            <div class="row row-l">
                <div class="image">
                    <img src="../assets/bg.png" alt="Background">
                </div>
            </div>
            <div class="row row-r">
                <div class="col">
                    <div class="title">
                        <span class="line"></span>
                        <h1>Bio</h1>
                    </div>
                    <div class="bio">
                        <p>I'm Tobi.</p>
                        <p>Creative web developer,</p>
                        <p>UI / 3D designer, and illustrator.</p>
                        <p>I craft beautiful web interfaces with code</p>
                        <p>and digital tools</p>
                        <p>Currently working on side projects.</p>
                        <p>You can find some of my experiments - <a href="http://codepen.io/cupofmint" data-l="here">here</a></p>
                        <p>Feel free to contact me</p>
                    </div>
                </div>
                <div class="col col_group">
                    <div class="row skills">
                        <div class="title">
                            <span class="line"></span>
                            <h1>Skills</h1>
                        </div>
                        <ul class="list">
                            <li v-for="skill in skills">{{ skill.name }}</li>
                        </ul>
                    </div>
                    <div class="row contact">
                        <div class="title">
                            <span class="line"></span>
                            <h1>Contact</h1>
                        </div>
                        <ul class="list">
                            <li v-for="link in links">
                                <a target="_blank"v-bind:href="`${link.url}`" class="link" :data-l="link.name">{{ link.name }}</a>
                            </li>
                            
                        </ul>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import 'gsap'
import animation from '../Animation'
export default {
    name: 'About',
    data() {
        return {
            name: 'Hola About',
            bgName: 'folio',
            skills: [
                { name: 'Javascript - ES6', percent: ''},
                { name: 'Python', percent: ''},
                { name: 'Java', percent: ''},
                { name: 'Illustrator, Photoshop', percent: ''},
                { name: 'Blender', percent: ''},
                { name: 'VueJS', percent: ''},
                { name: 'ReactJS', percent: ''},
            ],
            links: [
                { name: 'Mail', url: 'mailto:tobybarlhogun@gmail.com' },
                { name: 'Github', url: 'http://github.com/cupofmint' },
                { name: 'Codepen', url: 'http://codepen.io/cupofmint' },
                { name: 'Twitter', url: 'http://twitter.com/boredpill' },
                { name: 'Instagram', url: 'http://instagram.com/cupof_crazy' },
            ]
        }
    },
    methods: {
        enter(el, done) {
            animation.about.enter(el, done);
            // this.interactive()
        },
        leave(el, done) {
            animation.about.leave(el, done);
        },
    }
}
</script>

<style scoped>
#about {

}
.flex-container {
    width: 100%;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
}

.row {
    width: 50%;
    height: 100%;
}

.row-l {

}

.row-r, .row-l {
    display: flex;
    justify-content: center; 
    flex-direction: column;
}

.row-l {
   align-items: center;
  
}

img {
    width: 400px;
    user-select: none;
    pointer-events: none;
}

.col {
    margin: 3em 0;
}

.col_group {
    display: flex;
}

.bio p {
    margin: .7em 0;
    color: #FFF;
    font-size: .9rem;
}

.title {
    position: relative;
    text-transform: uppercase;
    font-size: .3rem;
    color: #494949;
    letter-spacing: 1em;
    margin-bottom: 5em;
}

.title span.line {
    width: 15px;
    height: 1px;
    background: #494949;
    position: absolute;
    top: 5px;
    left: -30px;
}


ul li {
    list-style-type: none;
    display: block;
    font-size: .9em;
    color: #FFF;
    margin: .7em 0;
}

.bg {
    position: absolute;
    top: 3em;
    right: 1em;
    font-family: HK Grotesk, 'sans-serif';
    text-transform: lowercase;
    color: #FFFFFF;
    font-size: 4rem;
    z-index: 999;
    transform: rotate(90deg)
}

.bg .text_container {
    overflow: hidden;
}

.line {
    width: 70px;
    height: 2px;
    background-color: #FFFFFF;
    position: absolute;
    top: 50%;
    left: calc(100% + 1em)
}
.image {
   
}

/* Responsiveness */
@media screen and (max-width: 1024px) {
    #about {
        font-size: 12px;
        height: 1000px;
    }
    img {
        width: 300px;
        margin-left: -3em;
    } 
    .bio p {
        font-size: 12px;
    }
}
@media screen and (max-width: 900px) {
    .flex-container {
        flex-direction: column;
    }
    .row {
        width: 100%;
    }
    .row-l {
        margin-top: 10em;
    }
    .row-r {
        margin-left: 9em;
        margin-bottom: 10em;
    }
    img {
        width: 300px;
    }  
}

@media screen and (max-width: 600px) {
    .bg {
        font-size: 3rem;
    }
    .flex-container {
        flex-direction: column;
    }
    .row {
        width: 100%;
    }
    .row-l {
        margin-top: 10em;
    }
    .row-r {
        margin-left: 9em;
        margin-bottom: 4em;
        margin-top: 8em;
    }
    img {
        width: 200px;
    } 
    .text_container {
        font-size: 2rem;
    }
}



</style>


