import 'gsap'

export default {
    // Home Animations
    home: {
        enter(el, done) {
            let tl = new TimelineMax({ onComplete: done });
            let h1 = Array.from(el.querySelectorAll('h1 .char'));
            let para = Array.from(el.querySelectorAll('.tagline p'));

            tl.from(el, 1, {
                y: 0,
                autoAlpha: 0,
                
            })
            .add('timer')
            .staggerFrom(h1, 1, {
                y: 10,
                autoAlpha: 0,
                ease: Power3.easeInOut
               
            }, .05, 'timer')
            .staggerFrom(para, 1, {
                rotation: -5,
                transformOrigin: 'right',
                y: 10,
                autoAlpha: 0,
                ease: Power3.easeInOut
               
            }, .1, 'timer')
        },
        leave(el, done) {
            let tl = new TimelineMax({ onComplete: done });
            let h1 = Array.from(el.querySelectorAll('h1 .char'));
            let para = Array.from(el.querySelectorAll('.tagline p'));

            tl.staggerTo(h1, .3, {
                y: -10,
                autoAlpha: 0,
                ease: Power3.easeInOut
               
            }, 0.03)
            .staggerTo(para, .5, {
                rotation: 5,
                transformOrigin: 'right',
                y: -10,
                autoAlpha: 0,
                ease: Power3.easeInOut
               
            }, 0.05)
            .to(el, .1, {
                y: -30,
                autoAlpha: 0,
                
            })
        }
    },

    // About Animations
    about: {
        enter(el, done) {
            // Variables
            let tl = new TimelineMax({ onComplete: done });
            let bg = Array.from(el.querySelectorAll('.bg .text_container .char'));
            let line = el.querySelector('.line');
            let title = Array.from(document.querySelectorAll('.title'));
            let image = document.querySelector('.image');
            let para  = Array.from(el.querySelectorAll('.bio p'));
            let skills  = Array.from(el.querySelectorAll('.skills ul li'));
            let contacts  = Array.from(el.querySelectorAll('.contact ul li'));

            //Animation begins
            tl.from(el, .1, {
                y: 30,
                autoAlpha: 0,
                ease: Expo.easeInOut
            })
            .from(image, 1, {
                x: '50%',
                autoAlpha: 0,
                ease: Expo.easeInOut,
                transformOrigin: 'left'
            })
            .staggerFrom(bg, 1, {
                y: '100%',
                ease: Power3.easeInOut
            }, .05)
            .from(line, .5, {
                scaleX: 0,
                transformOrigin: 'left',
                ease: Expo.easeInOut
            })
            .add('timer')
            .staggerFrom(para, .5, {
                y: 20,
                autoAlpha: 0,
                ease: Expo.ease
            }, .05, 'timer')
            .staggerFrom(skills, .5, {
                y: 20,
                autoAlpha: 0,
                ease: Expo.ease
            }, .05, 'timer')
            .staggerFrom(contacts, .5, {
                y: 20,
                autoAlpha: 0,
                ease: Expo.ease
            }, .05, 'timer')
            .staggerFrom(title, .5, {
                x: 20,
                autoAlpha: 0,
                ease: Expo.ease
            }, .05, 'timer')
            
            
        },
        leave(el, done) {
            let tl = new TimelineMax({ onComplete: done });
            let bg = Array.from(el.querySelectorAll('.bg .text_container .char'));
            let line = el.querySelector('.line');
            let titles = Array.from(document.querySelectorAll('.title'));
            let para  = Array.from(el.querySelectorAll('.bio p'));
            let skills  = Array.from(el.querySelectorAll('.skills ul li'));
            let contacts  = Array.from(el.querySelectorAll('.contact ul li'));
            
            tl.add('timer')
            .staggerTo(para, 1, {
                y: -10,
                autoAlpha: 0,
                ease: Quart.easeInOut
            }, .05, 'timer')
            .staggerTo(skills, .5, {
                y: -10,
                autoAlpha: 0,
                ease: Quart.easeInOut
            }, .05, 'timer')
            .staggerTo(contacts, .5, {
                y: -10,
                autoAlpha: 0,
                ease: Quart.easeInOut
            }, .05, 'timer')
            .staggerTo(titles, .5, {
                x: -10,
                autoAlpha: 0,
                ease: Quart.easeInOut
            }, .05, 'timer')
            .add('final')
            .staggerTo(bg, 1, {
                y: '-100%',
                ease: Power3.easeInOut
            }, -0.05, 'final')
            .to(line, .5, {
                scaleX: 0,
                transformOrigin: 'right',
                ease: Expo.easeInOut
            }, 'final')
            
            .to(el, 1, {
                y: -30,
                autoAlpha: 0,
                ease: Expo.easeInOut,
                transformOrigin: 'left'
            }, 'final')
           
        }
    }
}


