<template>
  <div id="app">
    <div id="wrapper">
      
      <div class="mouse">
        <nav>
          <router-link name="Main" to="/" class="link logo" exact data-l="cpfmnt">cpfmnt</router-link>
          <router-link name="About" to="/about" class="link about" data-l="About">About</router-link>
        </nav>
        <div>
        <transition v-on:enter="enter" v-on:leave="leave" mode="out-in" v-bind:css="false" appear>
          <router-view ref="view"></router-view>
        </transition>
        </div>
      </div>
    </div>
    
  </div>
</template>

<script>
import 'gsap'
// import Loader from './components/Loader.vue'
// import Intro from './loader'
export default {
  name: 'App',
  data() {
    return {
      
    }
  },
  components: {
    
  },
  methods: {
    enter(el, done) {
      this.$refs.view.enter(el, done);
      this.interactive()
    },
    leave(el, done) {
      this.$refs.view.leave(el, done);
    },
    interactive() {
      let main = document.getElementById('app')
      let block = document.querySelector('.mouse');

      console.log({
          left: block.offsetLeft,
          top: block.offsetTop,
          width: block.offsetWidth,
          height: block.offsetHeight,
          clientTop: block.clientTop,
          clientLeft: block.clientLeft
      })

      main.addEventListener('mousemove', (e) => {
        let xPos = (e.clientX / window.innerWidth) - 0.5;
        let yPos = (e.clientY / window.innerHeight) - 0.5;
          TweenMax.to(block, 1, {
              x: xPos * 50,
              y: yPos * 50,
              rotationY: xPos * 5,
              rotationX: yPos * 5,
              skewX: yPos * 2,
              skewY: xPos * 2,
              transformPerspective: 500,
              transformOrigin: 'center',
              ease: Expo.ease
          })
          // console.log((e.clientX / 20) - 20, (e.clientY / 20) -20)
      })

      main.addEventListener('mouseleave', (e) => {
          TweenMax.to(block, 1, {
              skewX: 0,
              skewY: 0,
              rotationX: 0,
              rotationY: 0
          })
      })
    },
    mounted() {
      console.log(this.interactive())
      const loader = new Intro(document.querySelector('#loader'));
      console.log(loader)
      loader.init()
    }
  }
}
</script>

<style>

/* FONTS */
@font-face {
  font-family: 'HK Grotesk';
  src: url("assets/fonts/HKGrotesk-Light.woff");
  /* src: 
  url("assets/fonts/HKGrotesk-Light.WOFF") format("woff"),
  url("assets/fonts/HKGrotesk-Medium.WOFF") format("woff"),
  url("assets/fonts/HKGrotesk-Bold.WOFF") format("woff"); */
}

@font-face {
  font-family: 'Playfair Display';
  src: url("assets/fonts/PlayfairDisplay-Regular.woff");
  /* src: 
  url("assets/fonts/HKGrotesk-Light.WOFF") format("woff"),
  url("assets/fonts/HKGrotesk-Medium.WOFF") format("woff"),
  url("assets/fonts/HKGrotesk-Bold.WOFF") format("woff"); */
}


* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
#app {
  width: 100%;
  height: 100vh;
  font-family: 'HK Grotesk', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #111111;
  user-select: none;
  overflow: hidden;
}
#wrapper {
  overflow: hidden;
}
nav {
  position: relative;
  color: #FFF;
  font-family: HK Grotesk;
  z-index: 1000;
}
nav .logo {
  position: absolute;
  top: 5em;
  left: 5em;
}
nav .about {
  position: absolute;
  top: 5em;
  right: 5em;
}
a {
  position: relative;
  color: transparent;
  text-decoration: none;
  transition: all .3 ease;
}
a.__active {
  color: #FFFFFF;
}
a.__active:before {
  content: '';
  width: 100%;
  height: 1px;
  background: #FFF;
  position: absolute;
  top: 9px;
  left: 0;
  transform-origin: right;
  transition: all .3s ease;
}

a:before, a:after {
  color: #AAAAAA;
  content: attr(data-l);
  position: absolute;
  top: 0;
  right: 0;
  transition: all .3s ease;
}

a:before {
  color: #FFF;
  transform: translateY(-10px);
  opacity: 0;
}
a:after {
  transform: translateY(0px);
  opacity: 1;
}
a:hover:before {
  transform: translateY(0px);
  opacity: 1;
  transition: all .3s ease-in-out;
}
a:hover:after {
  transform: translateY(10px);
  opacity: 0;
  transition: all .3s ease-in-out;
}


a.link:hover:before {
  left: 0;
  width: 100%;
  transform-origin: left;
  transition: all .3 ease;
}

.char {
    display: inline-block;
}

@media screen and (max-width: 900px) {
  #app {
    overflow: auto;
    
  }
}

@media screen and (max-width: 500px) {
  nav {
    font-size: .8rem;
  }
  nav .logo {
    top: 5em;
    left: 3em;
  }
  nav .about {
    top: 5em;
    right: 3em;
  }
}

</style>



